.sect_6 {
	position: relative;

	&__image {
		width: 45%;
		position: absolute;
		top: 0%;
		right: 0%;
	}

	.heat {
		display: flex;
		padding: 150px 0 0 0;

		&__col-1 {
			width: 49.9%;

			.title {
				color: #d88e9b;
				font-family: "Noto Sans JP", sans-serif;
				font-size: 32px;
				font-weight: 600;
			}

			.content {
				font-family: "Noto Sans JP", sans-serif;
				font-size: 18px;
				font-weight: 500;
				padding: 0 0 40px 0;
			}
		}

		&__col-2 {
			width: 49.9%;
		}
	}
}
